.start-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  padding: 20px 20px;
  background-color: aliceblue;
}
.start-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.start-button {
  background-color: #4caf50;
  margin: 0 0 1rem 0;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  border-radius: 1rem;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.start-button:hover {
  background-color: #ffffff;
  color: rgb(0, 0, 0);
  box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24),
    0 17px 50px 0 rgba(0, 0, 0, 0.19);
}

.question-container {
  height: 100%;
  padding: 20px 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.question-buttons {
  display: flex;
  gap: 1rem;
}
.question-exit {
  margin-left: 5rem;
}
.from-answer {
  padding: 1rem 0;
}
.point {
  padding-right: 15px;
  font-weight: bold;
}
.img {
  max-width: 100%;
  height: auto;
}
.labelText {
  font-weight: bolder;
  border-radius: 5px;
  padding: 3px;
}

.result-container {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem 2rem;
  background-color: aliceblue;
}
.result-header {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.result-header h2 {
  color: #4caf50;
}
.result-main {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.result-main h2 {
  color: red;
}
.result-main h3 {
  font-size: 1.5rem;
  color:#4caf50;
}
.cards-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
  padding: 2rem 0;
}
.card {
  display: flex;
  flex: 1 1 auto;
  flex-direction: column;
  border: 1px solid #000000;
  border-radius: 1rem;
  width: 320px;
  background-color: white;
  padding: 1rem;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.card-subtitle {
  list-style-type: none;
}

.q-wrong {
  border-radius: 5px;
  padding: 0.2rem;
  background-color: rgb(255 140 140);
}
.q-correct {
  border-radius: 5px;
  padding: 0.2rem;
  background-color: #a5dda5;
}
